body {
  display: flex;
  flex-direction: column;
  background: #E2F0CB;
  background-color: #E2F0CB;
  color: #343434;
  font-family: 'Courier New', Courier, monospace;
  font-size: 24px;
  text-shadow: 0px 1px 1px;
}

.header {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  align-self: center;
  width: 80%;
  font-size: 32px;
  font-weight: 550;
  margin-top: 35px;
  margin-bottom: 50px;
}

.header-title {
  flex: 1;
}

.header-info {
  display: flex;
  flex: 1;
  justify-content: flex-end;
}

.header-info > li:first-child {
  margin-left: auto;
}

.header-info > li {
  margin-right: 10px;
}

.header-info li button {
  background-color: transparent;
  border: none;
  color: #343434;
  cursor: pointer;
  font-family: 'Courier New', Courier, monospace;
  font-size: 28px;
  text-shadow: 0px 1px 1px;
}

.modal-background {
  display: none;
  opacity: 80%;
  background-color: #343434;
  position: fixed;
  height: 100%;
  width: 100%;
}

.about {
  display: none;
  flex-direction: column;
  align-items: center;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 50%;
  height: 40%;
  z-index: 1;
  overflow: auto;
  background-color: #E2F0CB;
  border: solid 3px #343434;
  border-radius: 10px;
  padding: 1%;
  line-height: 1.15;
}

.about-header {
  width: 100%;
}

#about-title {
  font-weight: 550;
  text-align: center;
  margin-bottom: 2.5%;
}

.about > p {
  margin-bottom: 2.5%;
}

.instructions {
  display: none;
  flex-direction: column;
  align-items: center;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 40%;
  height: 25%;
  z-index: 1;
  overflow: auto;
  background-color: #E2F0CB;
  border: solid 3px #343434;
  border-radius: 10px;
  padding: 1%;
  line-height: 1.15;
}

.instructions-header {
  width: 100%;
}

#instructions-title {
  font-weight: 550;
  text-align: center;
  margin-bottom: 2.5%;
}

.instructions > ul {
  width: 80%;
  list-style-type: disc;
}

.instructions ul li {
  margin-top: 10px;
  margin-bottom: 10px;
}

.close {
  color: #343434;
  float: left;
  font-size: 32px;
  font-weight: bold;
  /* padding: 0.5%; */
  left: 100%;
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

.graph {
  height: 350px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  border: solid 4px #343434;
  border-radius: 10px;
  width: 80%;
  margin-left: 10%;
  margin-bottom: 50px;
  background-color: #f0f0f0;
}

.bar {
  background: #8BD3E6;
  border: solid 0.5px #343434;
  width: 25px;
  margin: 0px 2.5px;
  transition: 0.2s all;
}

.row {
  display: flex;
  width: 80%;
  margin-left: 10%;
}

.settings {
  padding: 10px;
}

.settings > button {
  display: block;
  background-color: transparent;
  border: none;
  color: #343434;
  cursor: pointer;
  font-family: 'Courier New', Courier, monospace;
  font-size: 24px;
  font-weight: 400;
  text-shadow: 0px 1px 1px;
  margin-top: 10px;
  margin-bottom: 10px;
  margin-right: 100px;
  width: 100%;
}

.settings > button:first-child {
  margin-top: none;
  margin-bottom: 50px;
}

#arrsize {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}

#arrsize > input {
  display: block;
  padding: 20px;
  margin: auto;
}

.settings > button:hover {
  border: solid 1px black;
}

.settings > button:active {
  transform: translateY(4px);
}

.settings > input {
  padding: 10px;
}

.descriptions {
  border-left: solid 1px black;
  padding-left: 50px;
  flex: 50%;
}

.descriptions > h2 {
  font-size: 36px;
  font-weight: 550;
  margin-bottom: 20px;
}

.descriptions > p {
  line-height: 1.5;
}

footer {
  position: relative;
  align-self: center;
  bottom: 0;
  padding: 10px 10px;
}